			br17 = element("br");
			t31 = text("\n3.5. Před zasláním objednávky prodávajícímu je kupujícímu umožněno zkontrolovat a měnit údaje, které do objednávky kupující vložil, a to i s ohledem na možnost kupujícího zjišťovat a opravovat chyby vzniklé při zadávání dat do objednávky. Objednávku odešle kupující prodávajícímu kliknutím na tlačítko „ “. Údaje uvedené v objednávce jsou prodávajícím považovány za správné.\n");
			br18 = element("br");
			t32 = text("\n3.6. Odeslání objednávky se považuje za takový úkon kupujícího, který nepochybným způsobem identifikuje objednávané zboží, kupní cenu, osobu kupujícího, způsob úhrady kupní ceny, a je pro smluvní strany závazným návrhem kupní smlouvy. Podmínkou platnosti objednávky je správné vyplnění všech povinných údajů v objednávkovém formuláři, seznámení se s těmito obchodními podmínkami na webové stránce a potvrzení kupujícího o tom, že se s těmito obchodními podmínkami seznámil.\n");
			br19 = element("br");
			t33 = text("\n3.7. Prodávající neprodleně po obdržení objednávky toto obdržení kupujícímu potvrdí elektronickou poštou, a to na adresu elektronické pošty kupujícího uvedenou v uživatelském rozhraní či v objednávce (dále jen „elektronická adresa kupujícího“).\n");
