			t56 = text("\n4.7. Případné slevy z ceny zboží poskytnuté prodávajícím kupujícímu nelze vzájemně kombinovat. Objemová sleva 10% na další nákup není platná - nemůže být uplatněna, pokud je zboží nebo jeho část vrácena. \n");
			br40 = element("br");
			t57 = text("\n4.8. Je-li to v obchodním styku obvyklé nebo je-li tak stanoveno obecně závaznými právními předpisy, vystaví prodávající ohledně plateb prováděných na základě kupní smlouvy kupujícímu daňový doklad – fakturu. Prodávající plátcem daně z přidané hodnoty. Daňový doklad – fakturu vystaví prodávající kupujícímu po uhrazení ceny zboží a zašle jej v elektronické podobě na elektronickou adresu kupujícího.\n");
			br41 = element("br");
			t58 = space();
			h23 = element("h2");
			t59 = text("5. ODSTOUPENÍ OD KUPNÍ SMLOUVY");
			t60 = space();
			p5 = element("p");
			t61 = text("5.1. Kupující bere na vědomí, že dle ustanovení § 1837 občanského zákoníku, nelze mimo jiné odstoupit od kupní smlouvy:\n");
			br42 = element("br");
			t62 = text("\n5.1.1. o dodávce zboží, jehož cena závisí na výchylkách finančního trhu nezávisle na vůli prodávajícího a k němuž může dojít během lhůty pro odstoupení od smlouvy,");
			br43 = element("br");
			t63 = text("\n5.1.2. o dodání alkoholických nápojů, jež mohou být dodány až po uplynutí třiceti dnů a jejichž cena závisí na výchylkách finančního trhu nezávislých na vůli prodávajícího,");
			br44 = element("br");
			t64 = text("\n5.1.3. o dodávce zboží, které bylo upraveno podle přání kupujícího nebo pro jeho osobu");
			br45 = element("br");
			t65 = text("\n5.1.4. o dodávce zboží, které podléhá rychlé zkáze, jakož i zboží, které bylo po dodání nenávratně smíseno s jiným zbožím,");
			br46 = element("br");
			t66 = text("\n5.1.5. o dodávce zboží v uzavřeném obalu, které kupující z obalu vyňal a z hygienických důvodů jej není možné vrátit,");
			br47 = element("br");
			t67 = text("\n5.1.6. o dodávce zvukové nebo obrazové nahrávky nebo počítačového programu, pokud porušil jejich původní obal,");
			br48 = element("br");
			t68 = text("\n5.1.7. o dodávce novin, periodik nebo časopisů,");
			br49 = element("br");
			t69 = text("\n5.1.8. o dodání digitálního obsahu, pokud nebyl dodán na hmotném nosiči a byl dodán s předchozím výslovným souhlasem kupujícího před uplynutím lhůty pro odstoupení od smlouvy a prodávající před uzavřením smlouvy sdělil kupujícímu, že v takovém případě nemá právo na odstoupení od smlouvy.");
			br50 = element("br");
			t70 = text("\n5.2. Nejedná-li se o případ uvedený v čl. 5.1 či o jiný případ, kdy nelze od kupní smlouvy odstoupit, má kupující v souladu s ustanovením § 1829 odst. 1 občanského zákoníku právo od kupní smlouvy odstoupit, a to do čtrnácti (14) dnů od převzetí zboží, přičemž v případě, že předmětem kupní smlouvy je několik druhů zboží nebo dodání několika částí, běží tato lhůta ode dne převzetí poslední dodávky zboží. Odstoupení od kupní smlouvy musí být prodávajícímu odesláno ve lhůtě uvedené v předchozí větě.\n");
			br51 = element("br");
			t71 = text("\n5.3. Pro odstoupení od kupní smlouvy může kupující využit vzorový formulář poskytovaný prodávajícím, jenž tvoří přílohu obchodních podmínek. Odstoupení od kupní smlouvy může kupující zasílat mimo jiné na adresu provozovny nebo sídla prodávajícího. Pro doručování odstoupení od smlouvy platí ustanovení čl. 11 těchto obchodních podmínek.\n");
			br52 = element("br");
			t72 = text("\n5.4. V případě odstoupení od kupní smlouvy dle čl. 5.2 obchodních podmínek se kupní smlouva od počátku ruší. Zboží musí být prodávajícímu vráceno do čtrnácti (14) dnů od odstoupení od smlouvy prodávajícímu. Odstoupí-li kupující od kupní smlouvy, nese kupující náklady spojené s navrácením zboží prodávajícímu, a to i v tom případě, kdy zboží nemůže být vráceno pro svou povahu obvyklou poštovní cestou.\n");
			br53 = element("br");
			t73 = text("\n5.5. V případě odstoupení od smlouvy dle čl. 5.2 obchodních podmínek vrátí prodávající peněžní prostředky přijaté od kupujícího do čtrnácti (14) dnů od odstoupení od kupní smlouvy kupujícím, a to stejným způsobem, jakým je prodávající od kupujícího přijal. Prodávající je taktéž oprávněn vrátit plnění poskytnuté kupujícím již při vrácení zboží kupujícím či jiným způsobem, pokud s tím kupující bude souhlasit a nevzniknou tím kupujícímu další náklady. Odstoupí-li kupující od kupní smlouvy, prodávající není povinen vrátit přijaté peněžní prostředky kupujícímu dříve, než mu kupující zboží vrátí.\n");
			br54 = element("br");
			t74 = text("\n5.6. Nárok na úhradu škody vzniklé na zboží je prodávající oprávněn jednostranně započíst proti nároku kupujícího na vrácení kupní ceny.\n");
			br55 = element("br");
			t75 = text("\n5.7. Do doby převzetí zboží kupujícím je prodávající oprávněn kdykoliv od kupní smlouvy odstoupit. V takovém případě vrátí prodávající kupujícímu kupní cenu bez zbytečného odkladu, a to bezhotovostně na účet určený kupujícím.\n");
			br56 = element("br");
			t76 = text("\n5.8. Je-li společně se zbožím poskytnut kupujícímu dárek, je darovací smlouva mezi prodávajícím a kupujícím uzavřena s rozvazovací podmínkou, že dojde-li k odstoupení od kupní smlouvy kupujícím, pozbývá darovací smlouva ohledně takového dárku účinnosti a kupující je povinen spolu se zbožím prodávajícímu vrátit i poskytnutý dárek.\n");
			br57 = element("br");
			t77 = space();
			h24 = element("h2");
			t78 = text("6. PŘEPRAVA A DODÁNÍ ZBOŽÍ");
			t79 = space();
			p6 = element("p");
			t80 = text("6.1. V případě, že je způsob dopravy smluven na základě zvláštního požadavku kupujícího, nese kupující riziko a případné dodatečné náklady spojené s tímto způsobem dopravy.\n");
			br58 = element("br");
			t81 = text("\n6.2. Je-li prodávající podle kupní smlouvy povinen dodat zboží na místo určené kupujícím v objednávce, je kupující povinen převzít zboží při dodání.\n");
			br59 = element("br");
			t82 = text("\n6.3. V případě, že je z důvodů na straně kupujícího nutno zboží doručovat opakovaně nebo jiným způsobem, než bylo uvedeno v objednávce, je kupující povinen uhradit náklady spojené s opakovaným doručováním zboží, resp. náklady spojené s jiným způsobem doručení.\n");
			br60 = element("br");
			t83 = text("\n6.4. Při převzetí zboží od přepravce je kupující povinen zkontrolovat neporušenost obalů zboží a v případě jakýchkoliv závad toto neprodleně oznámit přepravci. V případě shledání porušení obalu svědčícího o neoprávněném vniknutí do zásilky nemusí kupující zásilku od přepravce převzít.\n");
			br61 = element("br");
			t84 = space();
			h25 = element("h2");
			t85 = text("7. PRÁVA Z VADNÉHO PLNĚNÍ");
			t86 = space();
			p7 = element("p");
			t87 = text("7.1. Práva a povinnosti smluvních stran ohledně práv z vadného plnění se řídí příslušnými obecně závaznými předpisy (zejména ustanoveními § 1914 až 1925, § 2099 až 2117 a § 2161 až 2174 občanského zákoníku).\n");
			br62 = element("br");
			t88 = text("\n7.2. Prodávající odpovídá kupujícímu, že zboží při převzetí nemá vady. Zejména prodávající odpovídá kupujícímu, že v době, kdy kupující zboží převzal:\n");
			br63 = element("br");
			t89 = text("\n7.2.1. má zboží vlastnosti, které si strany ujednaly, a chybí-li ujednání, má takové vlastnosti, které prodávající nebo výrobce popsal nebo které kupující očekával s ohledem na povahu zboží a na základě reklamy jimi prováděné,");
			br64 = element("br");
			t90 = text("\n7.2.2. se zboží hodí k účelu, který pro jeho použití prodávající uvádí nebo ke kterému se zboží tohoto druhu obvykle používá,");
			br65 = element("br");
			t91 = text("\n7.2.3. zboží odpovídá jakostí nebo provedením smluvenému vzorku nebo předloze, byla-li jakost nebo provedení určeno podle smluveného vzorku nebo předlohy,");
			br66 = element("br");
			t92 = text("\n7.2.4. je zboží v odpovídajícím množství, míře nebo hmotnosti a");
			br67 = element("br");
			t93 = text("\n7.2.5. zboží vyhovuje požadavkům právních předpisů.");
			br68 = element("br");
			t94 = text("\n7.3. Ustanovení uvedená v čl. 7.2 obchodních podmínek se nepoužijí u zboží prodávaného za nižší cenu na vadu, pro kterou byla nižší cena ujednána, na opotřebení zboží způsobené jeho obvyklým užíváním, u použitého zboží na vadu odpovídající míře používání nebo opotřebení, kterou zboží mělo při převzetí kupujícím, nebo vyplývá-li to z povahy zboží.\n");
			br69 = element("br");
			t95 = text("\n7.4. Projeví-li se vada v průběhu šesti měsíců od převzetí, má se za to, že zboží bylo vadné již při převzetí.\n");
			br70 = element("br");
			t96 = text("\n7.5. Práva z vadného plnění uplatňuje kupující u prodávajícího na adrese jeho provozovny, v níž je přijetí reklamace možné s ohledem na sortiment prodávaného zboží, případně i v sídle nebo místě podnikání. Za okamžik uplatnění reklamace se považuje okamžik, kdy prodávající obdržel od kupujícího reklamované zboží, tedy kdy dojde projev vůle kupujícího (uplatnění práva z vadného plnění) prodávajícímu\n");
			br71 = element("br");
			t97 = text("\n7.6. Další práva a povinnosti stran související s odpovědností prodávajícího za vady může upravit reklamační řád prodávajícího.\n");
			br72 = element("br");
			t98 = space();
			h26 = element("h2");
			t99 = text("8. DALŠÍ PRÁVA A POVINNOSTI SMLUVNÍCH STRAN");
			t100 = space();
			p8 = element("p");
			t101 = text("8.1. Kupující nabývá vlastnictví ke zboží zaplacením celé kupní ceny zboží\n");
			br73 = element("br");
			t102 = text("\n8.2. Prodávající není ve vztahu ke kupujícímu vázán žádnými kodexy chování ve smyslu ustanovení § 1826 odst. 1 písm. e) občanského zákoníku.\n");
			br74 = element("br");
			t103 = text("\n8.3. Mimosoudní vyřizování stížností spotřebitelů zajišťuje prodávající prostřednictvím elektronické adresy . Informaci o vyřízení stížnosti kupujícího zašle prodávající na elektronickou adresu kupujícího.\n");
			br75 = element("br");
			t104 = text("\n8.4. Prodávající je oprávněn k prodeji zboží na základě živnostenského oprávnění. Živnostenskou kontrolu provádí v rámci své působnosti příslušný živnostenský úřad. Dozor nad oblastí ochrany osobních údajů vykonává Úřad pro ochranu osobních údajů. Česká obchodní inspekce vykonává ve vymezeném rozsahu mimo jiné dozor nad dodržováním zákona č. 634/1992 Sb., o ochraně spotřebitele, ve znění pozdějších předpisů.\n");
			br76 = element("br");
			t105 = text("\n8.5. Kupující tímto přebírá na sebe nebezpečí změny okolností ve smyslu § 1765 odst. 2 občanského zákoníku.\n");
			br77 = element("br");
			t106 = space();
			h27 = element("h2");
			t107 = text("9. OCHRANA OSOBNÍCH ÚDAJŮ");
			t108 = space();
			p9 = element("p");
			t109 = text("9.1. Správcem osobních údajů poskytnutých za účelem plnění předmětu smlouvy je prodávající.\n");
			br78 = element("br");
