			br87 = element("br");
			t118 = space();
			h28 = element("h2");
			t119 = text("9.2. Účel zpracování, rozsah osobních údajů a právní základ pro zpracování");
			t120 = space();
			p10 = element("p");
			t121 = text("Kupující bere na vědomí, že pro účely uzavření kupní smlouvy, její následné plnění (zpracování objednávky, zajištění vyskladnění a doručení zboží) a případného řešení práv z vadného plnění (reklamací) správce zpracuje a uchová v souladu s nařízením Evropského parlamentu a Rady (EU) 2016/679, obecné nařízení o ochraně osobních údajů (dále jen GDPR), jeho osobní údaje v následujícím rozsahu: jméno, příjmení, adresa, e-mail a telefonní číslo, historii objednávek.\n");
			br88 = element("br");
			t122 = text("\nPrávním základem pro zpracování osobních údajů je v souladu s článkem 6 odst. 1 písm. b) GDPR plnění smlouvy, jejíž smluvní stranou je kupující.\n");
			br89 = element("br");
			t123 = text("\nKategorie příjemců/příjemci osobních údajů\n");
			br90 = element("br");
			t124 = text("\nSprávce se zavazuje osobní údaje kupujícího neposkytnout jiným subjektům než následujícím zpracovatelům:\n");
			br91 = element("br");
			t125 = text("\nsmluvnímu dopravci, kterého si kupující zvolí v objednávkovém formuláři, za účelem dodání zboží, přičemž údaje budou poskytnuty v následujícím rozsahu: jméno, příjmení, adresa, e-mail, telefonní číslo,\n");
			br92 = element("br");
			t126 = text("\nprovozovateli portálu Heureka.cz za účelem generování a zasílání dotazníků spokojenosti s nákupem v rámci programu Ověřeno zákazníky, do něhož je internetový obchod zapojen. Právním základem pro zpracování je v tomto případě v souladu s článkem 6 odst. 1 písm. f) GDPR oprávněný zájem správce, který spočívá ve zjišťování spokojenosti kupujícího s nákupem u správce. Zasílání dotazníků se týká všech kupujících, kteří neodmítli zasílání obchodních sdělení ve smyslu § 7 odst. 3 zákona o některých službách informační společnosti (č. 480/2004 Sb.), a je prováděno po každém nákupu na internetovém obchodu. Provozovatel portálu Heureka.cz je oprávněn předanou e-mailovou adresu použít výhradně za účelem vygenerování a zaslání dotazníku spokojenosti ve smyslu Podmínek programu Ověřeno zákazníky dostupných zde: sluzby.heureka.cz/napoveda/podminky-a-dulezite-dokumenty/. Proti zasílání e-mailových dotazníků v rámci programu Ověřeno zákazníky může kupující kdykoli vyjádřit námitku odmítnutím dalších dotazníků pomocí odkazu v e-mailu s dotazníkem. V případě námitky nebude dotazník kupujícímu dále zasílán.\n");
			br93 = element("br");
			t127 = space();
			h29 = element("h2");
			t128 = text("9.3. Doba uložení");
			t129 = space();
			p11 = element("p");
			t130 = text("Osobní údaje budou správcem uloženy po dobu nutnou ke splnění smlouvy (zpracování objednávky, vyskladnění a doručení zboží) a dále po dobu zákonné záruky (24 měsíců od převzetí zboží) či po dobu poskytnuté záruky smluvní.\n");
			br94 = element("br");
			t131 = text("\nKupující bere na vědomí, že správce má dle § 31 zákona o účetnictví (č. 593/1991 Sb.) povinnost uschovat účetní doklady a účetní záznamy (faktury) po dobu 5 let počínajících koncem účetního období, kterého se týkají (to znamená, pokud nakoupíte zboží v průběhu roku 2018, musí být faktura uchována do konce roku 2023). Správce má také povinnost vyplývající z § 47 zákona o správě daní a poplatků (č. 337/1992 Sb.) uchovat fakturu po dobu 3 let od konce zdaňovacího období, v němž vznikla daňová povinnost související s fakturou (to znamená, pokud nakoupíte zboží v průběhu roku 2018, musí být faktura pro účely daňového řízení uchována do konce roku 2021). Faktura obsahuje následující osobní údaje: jméno, příjmení, adresu, telefonní číslo, kontaktní e-mail.\n");
			br95 = element("br");
			t132 = text("\n   \n9.4. Práva kupujícího ve vztahu k osobním údajům\n");
			br96 = element("br");
			t133 = text("\nKupující dále bere na vědomí, že dle článků 15 až 21 GDPR má právo:\n");
			br97 = element("br");
			t134 = text("\n- na přístup k osobním údajům, které sestává z práva získat od správce potvrzení, zda osobní údaje, které se ho týkají, jsou či nejsou zpracovávány, a pokud je tomu tak, má právo získat přístup k těmto osobním údajům a k informacím vymezeným v článku 15 GDPR;\n");
			br98 = element("br");
			t135 = text("\n- na opravu nepřesných osobních údajů, které se ho týkají, dále s přihlédnutím k účelům zpracování má kupující právo na doplnění neúplných osobních údajů, a to i poskytnutím dodatečného prohlášení dle článku 16 GDPR;\n");
			br99 = element("br");
			t136 = text("\n- na výmaz („právo být zapomenut“), které spočívá v tom, že správce bez zbytečného odkladu vymaže osobní údaje, které se kupujícího týkají, jakmile již nebudou potřebné pro účely plnění smlouvy, pokud není dán jiný právní důvod pro jejich další zpracování;\n");
			br100 = element("br");
			t137 = text("\n- na omezení zpracování osobních údajů v případech vymezených článkem 18 GDPR;\n");
			br101 = element("br");
			t138 = text("\n- na přenositelnost údajů dle podmínek článku 20 GDPR;\n");
			br102 = element("br");
			t139 = text("\nvznést námitku proti zpracování osobních údajů dle článku 21 GDPR.\n");
			br103 = element("br");
			br104 = element("br");
			t140 = text("\nSprávce poskytne kupujícímu na žádost informace o přijatých opatřeních v každém případě nejpozději do 15 dnů od obdržení žádosti.\n");
			br105 = element("br");
			t141 = text("\nSprávce se zavazuje poskytnout kupujícímu v případě, že využije své právo na přístup k osobním údajům, bezplatně kromě první kopie zpracovávaných osobních údajů také bezplatnou druhou kopii, pokud o ni kupující požádá.\n");
			br106 = element("br");
			t142 = text("\nV případě pochybností o zpracovávání osobních údajů má kupující právo obrátit se na Úřad pro ochranu osobních údajů, který je v této oblasti dozorovým orgánem, a podat k němu stížnost.\n");
			br107 = element("br");
