			br110 = element("br");
			t149 = space();
			h211 = element("h2");
			t150 = text("11. DORUČOVÁNÍ");
			t151 = space();
			p13 = element("p");
			t152 = text("11.1. Oznámení týkající se vztahů prodávajícího a kupujícího, zejména týkající odstoupení od kupní smlouvy, musí být doručena poštou formou doporučeného dopisu, není-li v kupní smlouvě stanoveno jinak. Oznámení se doručují na příslušnou kontaktní adresu druhé strany a považují se za doručené a účinné okamžikem jejich dodání prostřednictvím pošty, s výjimkou oznámení o odstoupení od smlouvy učiněného kupujícím, kdy je odstoupení účinné, pokud je oznámení kupujícím ve lhůtě pro odstoupení odesláno.\n");
			br111 = element("br");
			t153 = text("\n11.2. Za doručené se považuje i oznámení, jehož převzetí bylo adresátem odmítnuto, které nebylo vyzvednuto v úložní době, nebo které se vrátilo jako nedoručitelné.\n");
			br112 = element("br");
			t154 = text("\n11.3. Smluvní strany můžou běžnou korespondenci vzájemně doručovat prostřednictvím elektronické pošty, a to na adresu elektronické pošty uvedenou v uživatelském účtu kupujícího či uvedenou kupujícím v objednávce, resp. na adresu uvedenou na webové stránce prodávajícího.\n");
			br113 = element("br");
			t155 = space();
			h212 = element("h2");
