			t120 = claim_space(section_nodes);
			p10 = claim_element(section_nodes, "P", {});
			var p10_nodes = children(p10);
			t121 = claim_text(p10_nodes, "Kupující bere na vědomí, že pro účely uzavření kupní smlouvy, její následné plnění (zpracování objednávky, zajištění vyskladnění a doručení zboží) a případného řešení práv z vadného plnění (reklamací) správce zpracuje a uchová v souladu s nařízením Evropského parlamentu a Rady (EU) 2016/679, obecné nařízení o ochraně osobních údajů (dále jen GDPR), jeho osobní údaje v následujícím rozsahu: jméno, příjmení, adresa, e-mail a telefonní číslo, historii objednávek.\n");
			br88 = claim_element(p10_nodes, "BR", {});
			t122 = claim_text(p10_nodes, "\nPrávním základem pro zpracování osobních údajů je v souladu s článkem 6 odst. 1 písm. b) GDPR plnění smlouvy, jejíž smluvní stranou je kupující.\n");
			br89 = claim_element(p10_nodes, "BR", {});
			t123 = claim_text(p10_nodes, "\nKategorie příjemců/příjemci osobních údajů\n");
			br90 = claim_element(p10_nodes, "BR", {});
			t124 = claim_text(p10_nodes, "\nSprávce se zavazuje osobní údaje kupujícího neposkytnout jiným subjektům než následujícím zpracovatelům:\n");
			br91 = claim_element(p10_nodes, "BR", {});
			t125 = claim_text(p10_nodes, "\nsmluvnímu dopravci, kterého si kupující zvolí v objednávkovém formuláři, za účelem dodání zboží, přičemž údaje budou poskytnuty v následujícím rozsahu: jméno, příjmení, adresa, e-mail, telefonní číslo,\n");
			br92 = claim_element(p10_nodes, "BR", {});
			t126 = claim_text(p10_nodes, "\nprovozovateli portálu Heureka.cz za účelem generování a zasílání dotazníků spokojenosti s nákupem v rámci programu Ověřeno zákazníky, do něhož je internetový obchod zapojen. Právním základem pro zpracování je v tomto případě v souladu s článkem 6 odst. 1 písm. f) GDPR oprávněný zájem správce, který spočívá ve zjišťování spokojenosti kupujícího s nákupem u správce. Zasílání dotazníků se týká všech kupujících, kteří neodmítli zasílání obchodních sdělení ve smyslu § 7 odst. 3 zákona o některých službách informační společnosti (č. 480/2004 Sb.), a je prováděno po každém nákupu na internetovém obchodu. Provozovatel portálu Heureka.cz je oprávněn předanou e-mailovou adresu použít výhradně za účelem vygenerování a zaslání dotazníku spokojenosti ve smyslu Podmínek programu Ověřeno zákazníky dostupných zde: sluzby.heureka.cz/napoveda/podminky-a-dulezite-dokumenty/. Proti zasílání e-mailových dotazníků v rámci programu Ověřeno zákazníky může kupující kdykoli vyjádřit námitku odmítnutím dalších dotazníků pomocí odkazu v e-mailu s dotazníkem. V případě námitky nebude dotazník kupujícímu dále zasílán.\n");
			br93 = claim_element(p10_nodes, "BR", {});
			p10_nodes.forEach(detach);
			t127 = claim_space(section_nodes);
			h29 = claim_element(section_nodes, "H2", {});
