			h29_nodes.forEach(detach);
			t129 = claim_space(section_nodes);
			p11 = claim_element(section_nodes, "P", {});
			var p11_nodes = children(p11);
			t130 = claim_text(p11_nodes, "Osobní údaje budou správcem uloženy po dobu nutnou ke splnění smlouvy (zpracování objednávky, vyskladnění a doručení zboží) a dále po dobu zákonné záruky (24 měsíců od převzetí zboží) či po dobu poskytnuté záruky smluvní.\n");
			br94 = claim_element(p11_nodes, "BR", {});
			t131 = claim_text(p11_nodes, "\nKupující bere na vědomí, že správce má dle § 31 zákona o účetnictví (č. 593/1991 Sb.) povinnost uschovat účetní doklady a účetní záznamy (faktury) po dobu 5 let počínajících koncem účetního období, kterého se týkají (to znamená, pokud nakoupíte zboží v průběhu roku 2018, musí být faktura uchována do konce roku 2023). Správce má také povinnost vyplývající z § 47 zákona o správě daní a poplatků (č. 337/1992 Sb.) uchovat fakturu po dobu 3 let od konce zdaňovacího období, v němž vznikla daňová povinnost související s fakturou (to znamená, pokud nakoupíte zboží v průběhu roku 2018, musí být faktura pro účely daňového řízení uchována do konce roku 2021). Faktura obsahuje následující osobní údaje: jméno, příjmení, adresu, telefonní číslo, kontaktní e-mail.\n");
			br95 = claim_element(p11_nodes, "BR", {});
			t132 = claim_text(p11_nodes, "\n   \n9.4. Práva kupujícího ve vztahu k osobním údajům\n");
			br96 = claim_element(p11_nodes, "BR", {});
			t133 = claim_text(p11_nodes, "\nKupující dále bere na vědomí, že dle článků 15 až 21 GDPR má právo:\n");
			br97 = claim_element(p11_nodes, "BR", {});
			t134 = claim_text(p11_nodes, "\n- na přístup k osobním údajům, které sestává z práva získat od správce potvrzení, zda osobní údaje, které se ho týkají, jsou či nejsou zpracovávány, a pokud je tomu tak, má právo získat přístup k těmto osobním údajům a k informacím vymezeným v článku 15 GDPR;\n");
			br98 = claim_element(p11_nodes, "BR", {});
			t135 = claim_text(p11_nodes, "\n- na opravu nepřesných osobních údajů, které se ho týkají, dále s přihlédnutím k účelům zpracování má kupující právo na doplnění neúplných osobních údajů, a to i poskytnutím dodatečného prohlášení dle článku 16 GDPR;\n");
			br99 = claim_element(p11_nodes, "BR", {});
			t136 = claim_text(p11_nodes, "\n- na výmaz („právo být zapomenut“), které spočívá v tom, že správce bez zbytečného odkladu vymaže osobní údaje, které se kupujícího týkají, jakmile již nebudou potřebné pro účely plnění smlouvy, pokud není dán jiný právní důvod pro jejich další zpracování;\n");
			br100 = claim_element(p11_nodes, "BR", {});
