			br105 = claim_element(p11_nodes, "BR", {});
			t141 = claim_text(p11_nodes, "\nSprávce se zavazuje poskytnout kupujícímu v případě, že využije své právo na přístup k osobním údajům, bezplatně kromě první kopie zpracovávaných osobních údajů také bezplatnou druhou kopii, pokud o ni kupující požádá.\n");
			br106 = claim_element(p11_nodes, "BR", {});
			t142 = claim_text(p11_nodes, "\nV případě pochybností o zpracovávání osobních údajů má kupující právo obrátit se na Úřad pro ochranu osobních údajů, který je v této oblasti dozorovým orgánem, a podat k němu stížnost.\n");
			br107 = claim_element(p11_nodes, "BR", {});
			t143 = claim_text(p11_nodes, "\nProdávající umožní nákup bez registrace, přičemž údaje neregistrovaných kupujících využije výhradně k plnění předmětu smlouvy, nikoliv k marketingovým či obchodním účelům.\n");
			br108 = claim_element(p11_nodes, "BR", {});
			t144 = claim_text(p11_nodes, "\nHEUREKA - OVĚŘENO ZÁKAZNÍKY  Vaši spokojenost s nákupem zjišťujeme prostřednictvím e-mailových dotazníků v rámci programu Ověřeno zákazníky, do něhož je náš e-shop zapojen. Ty vám zasíláme pokaždé, když u nás nakoupíte, pokud ve smyslu § 7 odst. 3 zákona č. 480/2004 Sb. o některých službách informační společnosti jejich zasílání neodmítnete. Zpracování osobních údajů pro účely zaslání dotazníků v rámci programu Ověřeno zákazníky provádíme na základě našeho oprávněného zájmu, který spočívá ve zjišťování vaší spokojenosti s nákupem u nás. Pro zasílání dotazníků, vyhodnocování vaší zpětné vazby a analýz našeho tržního postavení využíváme zpracovatele, kterým je provozovatel portálu Heureka.cz; tomu pro tyto účely můžeme předávat informace o zakoupeném zboží a vaši e-mailovou adresu. Vaše osobní údaje nejsou při zasílání e-mailových dotazníků předány žádné třetí straně pro její vlastní účely. Proti zasílání e-mailových dotazníků v rámci programu Ověřeno zákazníky můžete kdykoli vyjádřit námitku odmítnutím dalších dotazníků pomocí odkazu v e-mailu s dotazníkem. V případě vaší námitky vám dotazník nebudeme dále zasílat.“\n");
			br109 = claim_element(p11_nodes, "BR", {});
