			var p13_nodes = children(p13);
			t152 = claim_text(p13_nodes, "11.1. Oznámení týkající se vztahů prodávajícího a kupujícího, zejména týkající odstoupení od kupní smlouvy, musí být doručena poštou formou doporučeného dopisu, není-li v kupní smlouvě stanoveno jinak. Oznámení se doručují na příslušnou kontaktní adresu druhé strany a považují se za doručené a účinné okamžikem jejich dodání prostřednictvím pošty, s výjimkou oznámení o odstoupení od smlouvy učiněného kupujícím, kdy je odstoupení účinné, pokud je oznámení kupujícím ve lhůtě pro odstoupení odesláno.\n");
			br111 = claim_element(p13_nodes, "BR", {});
